<template>
  <div class="library-edit">
    <head-top title="题库编辑">
      <span slot="right" @click="saveData">
        <van-icon name="success" class="head-right"></van-icon>
      </span>
    </head-top>

    <van-cell-group>
      <van-field required placeholder="题库名称" maxlength="80" v-model="libraryName" :error-message="libraryNameErr">
      </van-field>
    </van-cell-group>
  </div>
</template>

<script>
export default {
  name: "libraryEdit",
  data () {
    return {
      libraryName: this.$store.getters.practiseLibrary ? this.$store.getters.practiseLibrary.name : "",
      libraryNameErr: "",
      libraryID: this.$store.getters.practiseLibrary ? this.$store.getters.practiseLibrary.id : ""
    };
  },
  methods: {
    saveData () {
      this.libraryNameErr = "";
      if (this.libraryName === "") {
        this.libraryNameErr = "请输入题库名称";
        return;
      }
      this.$mainHttp.classPractise.LibraryAdd({
        token: this.$store.getters.token,
        plID: this.libraryID,
        plType: "",
        plTitle: this.libraryName
      }).then(res => {
        if (res.Result) {
          this.$toast("保存成功");
          let practiseLibrary = this.$store.getters.practiseLibrary || {};
          if (res.Data) practiseLibrary.id = res.Data;
          practiseLibrary.name = this.libraryName;
          this.$store.commit("SET_PRACTISE_LIBRARY", practiseLibrary);
          this.$router.replace("/practise-library/edit");
        } else if (res.Info) {
          this.$toast(res.Info);
        } else {
          this.$toast("保存失败");
        }
      }).catch(() => {
        this.$toast("操作失败");
      });
    }
  }
}
</script>
